.navbarItems {
  /* background: linear-gradient(90deg, rgb(161, 151, 250) 0%, rgb(67, 57, 248) 100%); */
  background: #000;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  z-index: -1;
}

.navbar-logo {
  color: #000;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
}

li {
  text-decoration: none;
  list-style: none;
}

.DIAZSIFONTES {
  font-size: 1.4rem;
  width: 200px;
  color: #fff;
}

.navLinksLeft {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  background-color: #000;
  color: #fff;
}
.navLinksRight {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  background-color: #000;
  color: #fff;
}

.fa-bolt {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.nav-links:hover {
  background-color: #cdcdcd;
  border-radius: 4px;
  transform: all 0.2 ease-out;
  color: #000;
}

.fa-bars {
  color: #fff;
}

.active {
  color: #cdcdcd;
  text-decoration: underline !important;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media only screen and (max-width: 768px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 500px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #cdcdcd;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 0.2rem;
    width: 100%;
    display: table;
}

  .nav-links:hover {
    background-color: #cdcdcd;
    border-radius: 0;
    color: #000;
  }

  .navbar-logo {
    position: absolute;
    color: #fff;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #cdcdcd;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background-color: #fff;
    color: #000;
    transition: 250ms;
  }

  Button {
    display: none;
  }
}
